/* application detail style  */
.content2 {
  padding: 1rem 2rem;
  background: #fafafa;
  border-top: 0.25rem solid #eaeaea;
  border-bottom: 0.25rem solid #eaeaea;
}
.content2 h6,
.content3 h6 {
  font-weight: 500;
  font-size: 0.95rem;
  line-height: 17px;
  color: #121212;
  margin: 0;
}

.appinforow,
.appinforow1,
.appinforow2 {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}
.appinforowred {
  background: rgba(221, 46, 68, 0.2);
}
.content3 {
  background: #fafafa;
}
.appinforow1 {
  padding: 1rem 2rem;
}
.appinforow2 {
  padding: 0.4rem 2rem;
  align-items: center;
}
.appinforow2 h6 {
  margin-bottom: 0;
}
/* // new style for applicant card. */
.oac_container {
  padding: 0.4vw 0.5vw;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 0.5px solid #eaeaea;
  cursor: pointer;
  gap: 1rem;
  width: 100%;
}
.oac_container:hover {
  background: rgba(50, 71, 141, 0.15);
}
.oac_container_inner {
  display: flex;
  gap: 1.5vw;
  align-items: center;
  width: 40%;
}
.oac_container_inner > img {
  height: 3rem;
  width: 3rem;
  border-radius: 50%;
}
.oac_container_inner_info > h6 {
  font-weight: 500;
  font-size: 0.75rem;
  line-height: 17px;
  margin-bottom: 0.2rem;
  color: #121212;
}
.oac_container_inner_info > p {
  font-weight: 400;
  font-size: 0.65rem;
  line-height: 15px;
  text-align: left;
  margin-bottom: 0;
  color: #121212;
}
.oac_container_contact {
  width: 30%;
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  gap: 0.3rem;
}
.oac_container_contact_inner {
  display: flex;
  align-items: center;
  gap: 0.35rem;
  justify-content: flex-start;
}
.oac_container_contact_inner > img {
  height: 0.8rem;
  width: auto;
}
.oac_container_contact_inner > p {
  font-weight: 500;
  font-size: 0.7rem;
  line-height: 16px;
  margin-bottom: 0;
  color: rgba(0, 0, 0, 0.9);
}
.oac_container_stuff {
  width: 22%;
  display: flex;
  align-items: center;
  gap: 0.6rem;
  justify-content: flex-end;
}
.oac_container_stuff > img,
.oac_container_stuff > a > img {
  width: 20px;
  height: 20px;
  cursor: pointer;
}
/* // for batch style  */
.admission_confirm_batch_card {
  display: flex;
  align-items: center;
  gap: 1rem;
  justify-content: flex-start;
}
.admission_confirm_batch_card:hover,
.admission_confirm_batch_card_active {
  background: rgba(50, 71, 141, 0.15);
}
.admission_confirm_batch_card > img {
  height: 3rem;
  width: 3rem;
}
